// loader
const body = document.body
window.addEventListener('load', ()=>{
   body.classList.add('loaded')  
})
// nav top select
if (document.querySelector('.select')) {
   var x, i, j, l, ll, selElmnt, a, b, c;
   x = document.getElementsByClassName('select');
   l = x.length;
   for (i = 0; i < l; i++) {
      selElmnt = x[i].getElementsByTagName('select')[0];
      ll = selElmnt.length;
      a = document.createElement('DIV');
      a.setAttribute('class', 'select-selected text');
      a.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML;
      x[i].appendChild(a);
      b = document.createElement('DIV');
      b.setAttribute('class', 'select-items select-hide text');
      for (j = 1; j < ll; j++) {
         c = document.createElement('DIV');
         c.innerHTML = selElmnt.options[j].innerHTML;
         c.addEventListener('click', function (e) {
            var y, i, k, s, h, sl, yl;
            s = this.parentNode.parentNode.getElementsByTagName('select')[0];
            sl = s.length;
            h = this.parentNode.previousSibling;
            for (i = 0; i < sl; i++) {
               if (s.options[i].innerHTML == this.innerHTML) {
                  s.selectedIndex = i;
                  h.innerHTML = this.innerHTML;
                  y = this.parentNode.getElementsByClassName('same-as-selected');
                  yl = y.length;
                  for (k = 0; k < yl; k++) {
                     y[k].removeAttribute('class');
                  }
                  this.setAttribute('class', 'same-as-selected');
                  break;
               }
            }
            h.click();
         });
         b.appendChild(c);
      }
      x[i].appendChild(b);
      a.addEventListener('click', function (e) {
         e.stopPropagation();
         closeAllSelect(this);
         this.nextSibling.classList.toggle('select-hide');
         this.classList.toggle('select-arrow');
      });
   }
   function closeAllSelect(elmnt) {
      var x, y, i, xl, yl, arrNo = [];
      x = document.getElementsByClassName('select-items');
      y = document.getElementsByClassName('select-selected');
      xl = x.length;
      yl = y.length;
      for (i = 0; i < yl; i++) {
         if (elmnt == y[i]) {
            arrNo.push(i)
         } else {
            y[i].classList.remove('select-arrow');
         }
      }
      for (i = 0; i < xl; i++) {
         if (arrNo.indexOf(i)) {
            x[i].classList.add('select-hide');
         }
      }
   }
   document.addEventListener('click', closeAllSelect);
}
// burger
if (document.querySelector('.burger')) {
   function burgerMenu() {
      const burger = document.querySelector('.burger')
      const menu = document.querySelector('.nav__menu')
      const navTop = document.querySelector('.nav')
      const body = document.querySelector('body')
      burger.addEventListener('click', () => {
         if (!menu.classList.contains('active')) {
            menu.classList.add('active')
            navTop.classList.add('active')
            burger.classList.add('active')
            body.classList.add('locked')
         } else {
            menu.classList.remove('active')
            navTop.classList.remove('active')
            burger.classList.remove('active')
            body.classList.remove('locked')
         }
      })
   }
   burgerMenu();
}
// modals
if (document.querySelector('.modal__wrapper')) {
   const btns = document.getElementsByClassName('modal-btn');
   const spans = document.getElementsByClassName('modal__close')
   const modals = document.getElementsByClassName('modal__wrapper');
   [...btns].forEach((btn, ind) => {
      btn.onclick = () => (modals[ind].style.display = 'flex');
   });
   [...spans].forEach((span, ind) => {
      span.onclick = () => (modals[ind].style.display = 'none');
   });
   window.onclick = (e) => {
      [...modals].forEach((modal) => {
         if (e.target === modal) {
            modal.style.display = 'none';
         }
      });
   };
}
