// zone tabs
if (document.querySelector('.zone__tab')) {
   window.addEventListener("load", function () {
      // Patent tabs
      var theTabs = document.querySelectorAll(".zone__tab-content > a");
      function theTabClicks(tabClickEvent) {
         var clickedTab = tabClickEvent.currentTarget;
         var tabParent = tabClickEvent.currentTarget.parentNode.parentNode.parentNode;
         var theTabs = tabParent.querySelectorAll(".zone__tab-content > a");
         for (var i = 0; i < theTabs.length; i++) {
            theTabs[i].classList.remove("active");
         }
         clickedTab.classList.add("active");
         tabClickEvent.preventDefault();
         var contentPanes = tabParent.querySelectorAll(".zone__tab-item");
         for (i = 0; i < contentPanes.length; i++) {
            contentPanes[i].classList.remove("active");
         }
         var anchorReference = tabClickEvent.target;
         var activePaneId = anchorReference.getAttribute("href");
         var activePane = tabParent.querySelector(activePaneId);
         activePane.classList.add("active");
      }
      for (i = 0; i < theTabs.length; i++) {
         theTabs[i].addEventListener("click", theTabClicks)
      }
   })
}
// Child tabs
if (document.querySelector('.tab-item')) {
   function openTab(evt, tabName, boxName) {
      let i, tabcontent, tablinks;
      let box = document.getElementById(boxName)
      tabcontent = box.getElementsByClassName('tab-item');
      for (i = 0; i < tabcontent.length; i++) {
         tabcontent[i].classList.remove('active')
      }
      tablinks = box.getElementsByClassName('tab-link');
      for (i = 0; i < tablinks.length; i++) {
         tablinks[i].className = tablinks[i].className.replace(' active', '');
      }
      document.getElementById(tabName).classList.add('active')
      evt.currentTarget.className += ' active';
   }
}
