//load more
if (document.querySelector('#more')) {
   var moreText = document.getElementById("more");
   var btnText = document.getElementById("moreBtn");
   btnText.addEventListener("click", () => {
      if (btnText.classList.contains('active')) {
         btnText.classList.remove('active')
         moreText.classList.remove('active')
         btnText.innerText = 'Показать больше'
      } else {
         btnText.innerText = 'Показать менше'
         btnText.classList.add('active')
         moreText.classList.add('active')
      }
   })
}
// price
if (document.querySelector('#price')) {
   $(":checkbox").change(function () {
      $(this).closest("tr").toggleClass("highlight", this.checked)
      var price = document.querySelector('.price__fixed')
      if ($("tr").hasClass('highlight')) {
         price.classList.add('active')
      } else {
         price.classList.remove('active')
      }
   })
}